  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.gallery-container .gallery-item {
  margin: 1em;
  -webkit-transition: .3s opacity ease-in-out;
  transition: .3s opacity ease-in-out;
  height: 100%;
