.lightbox-container {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
}

.lightbox {
  background: #dfdfdf;
  position: fixed;
  width: 75%;
  left: calc(25% / 2);
  top: calc(25% / 2);
  box-shadow: 0 0 10px 0px #333;
  overflow: auto;
}

.lightbox h2 {
  text-transform: uppercase;
  text-align: left;
  padding: .5em;
  margin: 0;
}

.lightbox .content {
  height: calc(100% - 3.5em);
}

.lightbox .content iframe {
  width: 100%;
  height: auto;
  border: none;
}

.lightbox .content .description {
  font-size: 1em;
  padding: 1em;
  margin: 0;
  text-align: left;
  overflow-y: auto;
}

.lightbox-header {
  display: flex;
  background-color: #fff;
  flex-wrap: wrap;
  justify-content: space-between;
  border-bottom: 2px solid #ccc;
  flex-direction: column;
  position: relative;
}

.lightbox-header .lightbox-title {
  padding: 0.5em;
}

.lightbox-header h2 {
  padding: 0;
}

.lightbox-header h4 {
  margin: 0;
  text-align: left;
  padding: 0;
  color: #323232;
}

.lightbox-header .controls {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  align-items: center;
  display: flex;
}

.close-lightbox {
  padding: .25em;
  color: #444;
  font-size: 2em;
  cursor: pointer;
}

.close-lightbox::after {
  font-family: fontawesome;
  content: "\f057";
}

.info-lightbox {
  padding: .25em;
  color: #444;
  font-size: 2em;
  cursor: pointer;
}

.info-lightbox::after {
  font-family: fontawesome;
  content: "\f05a";
}

.next-item {
  cursor: pointer;
  position: fixed;
  font-size: 3em;
  color: #ddd;
  top: calc(50% - 1em);
  right: calc(5% / 2);
}

.next-item::before {
  font-family: fontawesome;
  content: "\f054";
}

.prev-item {
  cursor: pointer;
  position: fixed;
  font-size: 3em;
  color: #ddd;
  top: calc(50% - 1em);
  left: calc(5% / 2);
}

.prev-item::before {
  font-family: fontawesome;
  content: "\f053";
}

@media (max-width: 800px), (max-height: 600px) and (min-height: 375px) and (max-width: 1050px) {
  .lightbox {
    height: auto;
    width: 90%;
    top: calc(10% / 2);
    left: 5%;
  }
  .next-item {
    top: auto;
    top: initial;
    bottom: 2%;
  }
  .next-item::before {
    content: "\f078";
  }
  .prev-item {
    top: auto;
    top: initial;
    bottom: 2%;
  }
  .prev-item::before {
    content: "\f077";
  }
}

@media (orientation: landscape) and (max-width: 750px) {
  .lightbox {
    height: auto;
    width: 60%;
    top: calc(10% / 2);
    left: 20%;
  }
}

.desc-toggle-false {
  display: none;
}

.desc-toggle-true {
  display: inherit;
}
/*# sourceMappingURL=Lightbox.css.map */
